import React from "react"
import styles from "../styles/projectLink.module.css"
import {Link} from "gatsby"

export default (props) => (
    <div style={{
        position: "absolute", left: props.left, top: props.top,
        width: props.width, height: props.width}}>
        <Link to={props.to} style={{background: props.background, position: "absolute",
            width: props.width, height: props.width}}/>
        <div className={styles.boldText} style={{
            top: props.width,
            marginTop: "26px"
        }}> {props.btext}
        </div>
        <div className={styles.text} style={{
            top: props.width,
            marginTop: "9px"
        }}> {props.children}
        </div>
    </div>
)